.register-form__btn {
  margin-top: 18px;
  padding: 0;
  height: 50px;
  outline: none;
  border: none;
  border-radius: 2px;
  cursor: pointer;
  background-color: #000000;
  color: #FFFFFF;
  font-size: 18px;
  font-weight: 400;
  line-height: 21.78px;
  text-align: center;
  transition: .8s;
  width: 350px;
  margin-top: 50px;
}

.register-form__btn:hover {
  opacity: .8;
}